var utils = require('./utils');

var _require = require('./model/transaction'),
    Payment = _require.Payment;

function bytesTokB(bytes) {
  return bytes / 1000;
}

function formatFee(fee, bytes) {
  var format = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : 'kB';
  var currency = arguments.length > 3 && arguments[3] !== undefined ? arguments[3] : 'btc';
  // TODO
  // takes in satoshi
  // if !currency
  // returns kilobytes
  return fee / bytesTokB(bytes);
}

function getFeeRate(transaction, fee) {
  var byteSize = utils.getByteLength(transaction.getRawString());
  return formatFee(fee, byteSize);
}

function getTotalFees(transaction, feeRate) {
  var byteSize = utils.getByteLength(transaction.getRawString());
  return feeRate * bytesTokB(byteSize);
}

function calculateChangePayment(balance, fee, address) {
  return new Payment(address, balance - fee);
}

module.exports = {
  formatFee: formatFee,
  getFeeRate: getFeeRate,
  getTotalFees: getTotalFees,
  calculateChangePayment: calculateChangePayment
};