var axios = require('axios');

function pushtx(tx) {
  var network = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'testnet';
  // TODO make this a switch
  var net = network.toUpperCase() === 'MAINNET' ? 'main' : 'test3';
  return axios.post("https://api.blockcypher.com/v1/btc/".concat(net, "/txs/push"), JSON.stringify({
    tx: tx
  }));
}

function pullUnspentTransactions(address) {
  var network = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'testnet';
  var net = network.toUpperCase() === 'MAINNET' ? 'main' : 'test3';
  return axios.get("https://api.blockcypher.com/v1/btc/".concat(net, "/addrs/").concat(address, "?unspentOnly=true")).then(function (response) {
    var txs = Array.isArray(response.data.txrefs) ? response.data.txrefs : [response.data.txrefs];

    if (!txs[0]) {
      return null;
    }

    txs = txs.reduce(function (acc, tx) {
      if (!acc.length) {
        return tx.tx_hash;
      }

      return "".concat(acc, ";").concat(tx.tx_hash);
    }, '');
    return axios.get("https://api.blockcypher.com/v1/btc/".concat(net, "/txs/").concat(txs));
  });
}

function pullMetadata() {
  var network = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : 'testnet';
  var net = network.toUpperCase() === 'MAINNET' ? 'main' : 'test3';
  return axios.get("https://api.blockcypher.com/v1/btc/".concat(net));
}

module.exports = {
  pushtx: pushtx,
  pullMetadata: pullMetadata,
  pullUnspentTransactions: pullUnspentTransactions
};